@import "./variables";

.card-view{
  font-family: $main-font;
  padding-top: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 1rem;
}

.image-wrapper{
  padding: 1rem;
}

.card-view-image{
  width: 100%;
  border-radius: .7rem;
  box-shadow: $shadow;
}

.card-view-info{
  display: flex;
  flex-direction: column;
  margin-bottom: 1.5rem;
  padding-left: 1.5rem;
}

#type-info{
  &::after{
    content: "";
    border-bottom: 1px solid lightgrey;
    align-self: left;
    width: 90%;
  }
}

#stats-info{
  &::after{
    content: "";
    border-bottom: 1px solid lightgrey;
    align-self: left;
    width: 90%;
  }
}

.card-view-title{
  font-weight: bold;
}

.card-view-text{
  padding: 0;
  margin: 0;
  margin-bottom: .7rem;
  font-weight: 500;
}

@media (min-width: 600px){
  .card-view{
    flex-direction: row;
  }

  .card-view-image{
    height: 80vh;
    width: auto;
  }
    
  .card-view-info{
    margin-top: 1.5rem;
  }
  
  #logout-card-view {
    height: 2.5rem;
  }
  
  #back-button{
    height: 2.5rem;
  }
}