@import "./variables";

.header{
  z-index: 1;
  position: fixed;
  width: 100%;
  box-shadow: $shadow-straight;
  font-family: $main-font;
  display: grid;
  grid-template-columns:  auto auto 1fr;
  grid-template-rows: 1fr;
  align-items: center;
  
  height: 3rem;
  background-color: $yellow;
}

.type-select{
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-areas:
  "selectText button"
  "typeList typeList";
  align-items:start;
  justify-items: end;

  width: auto;
  max-width: 200px;
  height: 2rem;
  background-color: $light-grey;
  color: grey;
  border-radius: 1rem;
  margin-left: 5px;
  transition: .3s ease-out;

  &.open {
    transition: .15s ease-in;
    border-radius: 1rem 1rem 0 0;
  }
}

.type-select-text{
  grid-area: selectText;
  font-size: .8rem;
  padding-top: 9px;
  padding-left: 5px;
  margin: 0;
  line-height: 100%;
}

.type-select-icon{
  position: relative;
  top: 5px;
  grid-area: button;
  font-size: 1.5rem;
  padding-right: 5px;
  transition: .3s ease;
  transform-origin: 8px 12px;

  &.open{
    transition: .1s ease-out;
    transform: rotate(-180deg)
  }

  &:hover{
    color: black;
  }
}

.type-list{
  grid-area: typeList;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  gap: .2rem;
  width: 100%;
  max-height: 250px;
  position: relative;
  padding-bottom: 15px;
  padding-top: 5px;
  top: 8px;
  background-color: $light-grey;
  border-radius: 0 0 1rem 1rem;
  transition: .15s ease-out;
  box-shadow: $shadow-transparent;

  &::-webkit-scrollbar{
    width: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, .2);
    border-radius: 50px;
  }

  &.closed{
    visibility: hidden;
    opacity: 0;
    position: relative;
    top: -30px;
    transition: .3s ease-in;
  }
}

.search{
  margin-bottom: 5px;
  border: none;
  border-bottom: 2px solid $yellow;
  background-color: transparent;
  line-height: 1.5rem;
  width: 90%;

  &:focus{
    outline: none;
  }
}

.type-item {
  padding-left: 7px;
  width: 95%;
  background-color: white;
  height: auto;
  border-radius: 1.5rem;
  padding-top: 3px;
  padding-bottom: 10px;
  transition: .1s ease;

  &:hover{
    background-color: $yellow;
  }
  &.selected {
    background-color: $blue;

    &:hover{
      background-color: $light-blue;
    }
  }
}

.type-item-text{
  padding-top: 5px;;
  margin: 0;
}

#logout{
  font-size: .8rem;
  margin: 0;
  margin-left: 5px;
  width: 60px;
  background-color: $light-grey;
  justify-self: end;

  &:hover{
    background-color: $light-yellow;
  }

  &:active{
    background-color: $dark-yellow;
  }
}