/* SHARED CSS */

/* BUTTON */

.button {
  font-family: $main-font;
  font-weight: 500;
  margin-top: 10px;
  border: none;
  border-radius: 50px;
  font-size: .9rem;
  background-color: $yellow;
  min-height: 2rem;
  width: 7rem;
  transition: .2s ease;

  &:hover{
    background-color: $light-yellow; 
  }

  &:active{
    background-color: $dark-yellow;
    transition: .1s ease;
  }
}