@import "./variables";

.card-wrapper:hover{
  .card{
    transform: translate(0px, -10px);
    box-shadow: 5px 5px 15px grey;
    transition: .1s ease-out;
  }
}

.card{
  font-family: $main-font;
  display: flex;
  flex-direction: column;
  gap: .5rem;
  padding: 1rem;
  background-color: $light-grey;
  width: 10rem;
  border-radius: 1rem;
  box-shadow: 3px 3px 5px grey;
  transition: .3s ease;
}

.card-image{
  border-radius: .5rem;
}

.card-name{
  padding: 0;
  margin: 0;
  font-size: 1.5rem;
}

.card-artist {
  color: grey;
  font-style: italic;
  padding: 0;
  margin: 0;
  font-size: .75rem;
}