.image-loading{
  width: 10rem;
  height: 221px;
  background: linear-gradient(
    83deg, 
    rgb(218, 218, 218) 0% 42%, 
    rgb(235, 235, 235) 50%, 
    rgba(218,218,218,1)  59% 100%);
  background-size: 300%;
  margin: auto;
  border-radius: .5rem;
  animation: move-background 1.5s infinite;
}

.name-loading {
  width: 9rem;
  height: 1.5rem;
  background: linear-gradient(
    75deg, 
    rgb(218, 218, 218) 0% 42%, 
    rgb(235, 235, 235) 50%, 
    rgba(218,218,218,1)  59% 100%);
  background-size: 300%;
  border-radius: 1rem;
  margin-bottom: 10px;
  animation: move-background 1.5s infinite;
}

.artist-loading {
  width: 7rem;
  height: .75rem;
  background: linear-gradient(
    75deg, 
    rgb(218, 218, 218) 0% 42%, 
    rgb(235, 235, 235) 50%, 
    rgba(218,218,218,1)  59% 100%);
  background-size: 300%;
  border-radius: 1rem;
  animation: move-background 1.5s infinite;
}

@keyframes move-background{
  0%{
    background-position: left;
  }

  100%{
    background-position: right;
  }
}