@import "./variables";

.login-wrapper{
  display: flex;
  height: 85vh;
  justify-content: center;
  align-items: center;
}

.login {
  font-family: $main-font;
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 250px;
  width: 80%;
  max-width: 450px;
  align-items: center;
  justify-content: end;
  gap: 10px;
  border-radius: 1rem;
  box-shadow: $shadow;
}

.input-wrapper{
  position: relative;
}

.input-field {
  border: none;
  border-bottom: 2px solid $yellow;
  background-color: transparent;
  height: 2rem;
  width: 12rem;

  &:focus{
    outline: none;
  }
  &:focus + .input-label{
    top: -5px;
    font-size: .7rem;
  }
  &:valid + .input-label{
    top: -5px;
    font-size: .7rem;
  }
}

.input-label{
  color: grey;
  position: absolute;
  font-size: .9rem;
  top: 10px;
  left: 0;
  z-index: -1;
  transition: .2s ease-in-out;
}

.failed-auth{
  border-bottom: 2px solid red;
}

.failed-auth-text{
  font-size: .8rem;
  font-weight: 500;
  color:red;
  position: relative;
  top: -5px;
  visibility: hidden;

  &.failed{
    visibility: visible;
  }
}