.otp-wrapper{
  display: flex;
  height: 85vh;
  justify-content: center;
  align-items: center;
}

.otp {
  font-family: $main-font;
  text-align: center;
  display: flex;
  flex-direction: column;
  height: auto;
  width: 80%;
  max-width: 450px;
  padding-top: 50px;
  padding-bottom: 50px;
  align-items: center;
  justify-content: end;
  gap: 10px;
  border-radius: 1rem;
  box-shadow: 5px 5px 20px grey;
}

#otp-header{
  padding: 0;
  margin: 0;
  font-size: 1.3rem;
}

#time-display{
  padding-bottom: 20px;
  margin: 0;
  font-size: .75rem;
  color: grey;
}

#verification-code-button{
  margin-top: 0;
  padding-top: 6px;
  padding-bottom: 6px;
  font-size: .8rem;
}