
#paginator{
  font-family: $main-font;
  font-weight: 600;
  font-size: .9rem;
  display: flex;
  flex-direction: row;
  width: 80%;
  max-width:300px;
  height: 2rem;
  background-color: $yellow;
  border-radius: 1rem 1rem 0 0;
  margin: auto;
  justify-content: center;
  align-items: center;
}

.page-link{
  margin-right: .5rem;
  border-radius: 5px;
  margin: 0;
  padding: 2px;
  padding-right: 6px;
  padding-left: 6px;
  transition: .2s ease;

  &:hover{
    background-color: black;
    color: white;
  }
}

.blank-pages{
  padding: 0;
  margin-left: 0px;
  margin-right: 0px;
}

.current-page{
  border: 2px solid black;
  border-radius: 5px;
  padding-right: 4px;
  padding-left: 4px;
}