#logout-card-view {
  font-size: .8rem;
  margin: 0;
  margin-right: 5px;
  width: 100px;
  background-color: $light-grey;
  justify-self: end;

  &:hover{
    background-color: $light-yellow;
  }

  &:active{
    background-color: $dark-yellow;
  }
}

