.card-display {
  display: grid;
  grid-template-columns: 1ft;
  grid-template-rows: auto;
  grid-gap: 1.5rem;
  min-height: 83vh;
  justify-items: center;
  align-items: center;
  padding: 1rem;
  padding-top: 5rem;
}

.not-found{
  position: absolute;
  top: 100px;

  font-family: $main-font;
  text-align: center;
  font-weight: 600;
  font-size: 3rem;
  margin: auto;
 }